@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');



/*########## Variables ##########*/
:root, :root.light-mode {
    --bg-color: #fff;
    --highlight-color: #25507f;
    --text-color-1: #0c3264;
    --text-color-2: #8b2781;
    --border-color: #eacbfe;
    --shadow-color: rgba(153,215,251,0.1);
}
:root.dark-mode {
    --bg-color: #0c3264;
    --highlight-color: #25507f;
    --text-color-1: #99d7fb;
    --text-color-2: #de88f0;
    --border-color: #eacbfe;
    --shadow-color: rgba(153,215,251,0.1);
}



/*########## Base ##########*/
* {
    /* outline: 1px solid red; */
    margin: 0;
    padding: 0;
    font-family: "Quicksand";
    color: var(--text-color-1);
}
body {
    background-color: var(--bg-color);
}



/*########## Navbar ##########*/
.nav-parent {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    margin: auto;
    z-index: 10;
}
.navbar {
    display: flex;
    background: var(--bg-color);
    align-items: center;
    max-width: 800px;
    height: 50px;
    margin: auto;
    padding: 20px;
    border-bottom: 1px solid var(--border-color);
}

/* Logo */
.navbar h1{
    height: 98%;
    width: 36%;
    margin: 1%;
    display: flex;
    justify-content: start;
    align-items: center;
}
.navbar h1 a {
    color: var(--text-color-2);
    text-decoration: none;
}

/* Links */
.navbar .links {
    margin-left: auto;
    height: 98%;
    width: 52%;
    margin: 1%;
    display: flex;
    justify-content: end;
    align-items: center;
}
.navbar .links a {
    display: inline-block;
    margin-left: 2%;
    text-decoration: none;
    padding: 2%;
    text-align: center;
}
.navbar .links a:hover {
    color: white;
    background-color: var(--highlight-color);
    border-radius: 8px;
}

/* Theme Button */
.theme-btn-wrapper {
    height: 98%;
    width: 4%;
    margin: 1%;
    display: flex;
    align-items: center;
    min-width: 30px;
    min-height: 30px;
}
.navbar .theme-btn {
    width: 100%;
    cursor: pointer;
    aspect-ratio: 1;
    background: none;
    border: none;
}
.light-btn, .dark-mode .dark-btn { display: none; }
.dark-btn, .dark-mode .light-btn {
    position: relative;
    font-size: 36px;
    line-height: 36px;
    display: block;
}
.theme-btn-wrapper svg {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.theme-btn-wrapper svg * {
    fill: var(--text-color-1);
}

/* Menu */
.navbar .menu-button {
    display: none;
}
.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    border-radius: 5px;
    background-color: var(--text-color-1);
    margin: 6px 0;
    transition: 0.4s;
}
.menu-show .bar1 { transform: translate(0, 11px) rotate(-45deg); }
.menu-show .bar2 { opacity: 0; }
.menu-show .bar3 { transform: translate(0, -11px) rotate(45deg); }



/*########## Content ##########*/
.content {
    max-width: 600px;
    margin: 90px auto 40px;
    padding: 20px;
}



/*########## Game Previews ##########*/
.game-preview {
    padding: 10px 16px;
    margin: 20px 0;
    border-bottom: 1px solid var(--border-color);
}
.game-preview:hover {
    box-shadow: 5px 7px 5px 0 var(--shadow-color);
    color: var(--text-color-1);
}
.game-preview h2 {
    font-size: 20px;
    color: var(--text-color-2);
    margin-bottom: 8px;
}



/*########## Mobile Width ##########*/
@media (max-width: 650px){
    .navbar .menu-button {
        display: block;
        margin-left: 5%;
    }
    .navbar .links {
        display: none;
    }
    .navbar .links.menu-show {
        display: block;
        position: fixed;
        top: 91px;
        left: 0;
        margin: 0;
        padding: 0;
        width: 100%;
    }
    .navbar .links.menu-show a {
        width: 100%;
        text-align: center;
        background: var(--bg-color);
        margin: 0;
        padding: 2% 0;
        font-size: 24px;
        border-bottom: 1px solid var(--border-color);
    }
    .navbar .links.menu-show a:hover {
        background: var(--bg-color);
        background-color: var(--highlight-color);
    }
    .theme-btn-wrapper {
        margin-left: auto;
    }
    .navbar .theme-btn {
        border: none;

    }
}
